.modal {
  position: fixed;
  inset: 0;
  z-index: 1;
  padding: 50px 0;

  &__overlay {
    position: absolute;
    inset: 0;
    background-color: rgba(43, 43, 43, 0.5);
  }
  &__content {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: 450px;
    // max-width: 30%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
    }
  }
  &__closeIcon {
    cursor: pointer;
    position: absolute;
    top: 25px;
    right: 25px;
    z-index: 1;
  }
}
