.breadcrumbs {
  display: flex;
  font-weight: 400;
  font-size: 16px;
  line-height: 122%;
  color: #333333;

  &__crumb{
    display: flex;
    align-items: center;
    margin-right: 15px;

    img{
        margin-right: 15px;
    }
  }

  @include xs-only {
    display: none;
  }
}
