.btn {
  height: 64px;
  background: #cf0a2c;
  color: white;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 27px;
  transition: all 0.3s;
  padding: 20px;
}
.btn:hover {
  background: white;
  border: 1px solid #cf0a2c;
  color: #cf0a2c;
}
.btn:hover svg {
  stroke: #cf0a2c;
}
