.footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: 40px 60px;
  background-color: #999999;
  color: #ffffff;

  &__arrow {
    display: none;
  }

  &__politic:hover {
    cursor: pointer;
    transition: all 0.3s;
    color: rgba(155, 29, 13, 1);
  }

  &__inner {
    width: 100%;
  }

  &__menu {
    display: flex;
    width: 100%;
    justify-content: space-between;
    max-width: 750px;
  }

  &__info {
    font-family: 'Circe';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
  }
  &__contacts {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    max-width: 250px;
    p {
      margin-bottom: 15px;
    }
  }

  &__contactTitle {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    border-bottom: 2px solid #cccccc;
    padding-bottom: 10px;
    width: 150px;
    margin-bottom: 30px;
  }

  &__about {
    display: flex;
    margin-bottom: 20px;
    transition: all 0.3s;

    svg {
      margin-right: 15px;
    }
    p {
      font-family: 'Circe';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 122%;
    }
  }

  &__about:hover {
    cursor: pointer;
    color: rgba(155, 29, 13, 1);
    path {
      fill: rgba(155, 29, 13, 1);
    }
  }

  @include sm-down {
    align-items: flex-start;
    font-size: 12px;

    &__arrow {
      display: inline;
      width: 15px;
      transition: all 0.3s;
    }

    &__arrow--active {
      transform: rotate(90deg);
    }

    &__menu {
      padding-top: 35px;
      margin-left: 30px;
      flex-direction: column;
      justify-content: space-around;
    }

    &__contactTitleContainer {
      display: flex;
    }

    &__contactTitle {
      border-bottom: 0;
      font-size: 14px;
      margin: 0;
      padding: 0;
    }

    &__info {
      font-size: 12px;
    }

    &__dropdown {
      margin-top: 15px;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.3s ease-out;
    }
    &__dropdown--active {
      max-height: 125px;
    }

    &__politic {
      width: 170px;
    }
  }

  @include xs-only {
    padding: 25px 15px;
  }
}
