.services {
  display: flex;
  &__title {
    width: 516px;
    height: 280px;
    font-size: 16px;
  }

  &__red-block {
    width: 401px;
    height: 401px;
    background: #cf0a2c;
  }
  &__group {
    display: flex;
    img {
      position: absolute;
      right: 36px;
      bottom: 36px;
    }
  }

  @media (max-width: 576px) {
    flex-direction: column;

    &__title {
      width: 350px;
      height: 280px;
      font-size: 14px;
    }
    &__red-block {
      width: 360px;
    }
    &__group {
      margin-top: 130px;
      margin-left: 10px;

      img {
        right: 8px;
        width: 360px;
        height: 401px;
      }
    }
  }
}
.ourAdvantages {
  background: #999999;
  padding: 60px;

  &__title {
    font-size: 35px;
    color: #ffffff;
  }

  &__list {
    margin-top: 40px;

    li {
      display: flex;
      align-items: center;
      column-gap: 15px;
      img {
        width: 24px;
        height: 19px;
        margin-bottom: 24px;
      }

      span {
        font-size: 16px;
        color: white;
        margin-bottom: 24px;
      }
    }
  }

  @media (max-width: 576px) {
    &__title {
      font-size: 25px;
    }
  }
}
.maintenanceServices {
  margin: 60px;
  &__title {
    font-size: 35px;
    width: 516px;
  }

  @media (max-width: 576px) {
    margin: 40px;
    &__title {
      font-size: 25px;
      width: 400px;
    }
  }
}
.stagesOfWorkOrganization {
  @media (max-width: 576px) {
    margin: 90px 40px;
    &__title {
      font-size: 25px;
      width: 330px;
    }

    &__lists-block {
      display: flex;
      flex-direction: column;
    }
  }
}
.finishedProjects {
  @media (max-width: 576px) {
    margin: 90px 30px;
    &__block {
      &__title {
        font-size: 22px;
        width: 400px;
      }
      &__desc {
        font-size: 14px;
        width: 200px;
      }
    }

    &__list {
      flex-direction: column;
      column-gap: 0;

      li {
        p {
          margin-bottom: 30px;
        }
        img {
          margin-bottom: 10px;
        }
      }
    }
  }
}
.feedBackBlock {
  @media (max-width: 576px) {
    width: 90%;
    padding: 0;

    margin: 20px;

    .feedBackBlock__p-desc {
      display: none;
      width: 0;
    }
    &__group {
      flex-direction: column;
      padding-left: 30px;
    }
  }
}
