.project {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(auto-fill, 1fr);
  grid-gap: 24px;
  align-items: center;
  margin: 40px 0;

  @media (max-width: 576px) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(auto-fill, 1fr);
    margin: 40px 15px;
  }
}

.project .title {
  margin: 40px 0;
  font-family: 'Circe';
  font-weight: 700;
  font-size: 35px;
  line-height: 124%;
  color: #333333;
  margin-bottom: 36px;
}
@media (max-width: 576px) {
  .project .title {
    margin-top: 60px;
  }
}

.project__block {
  background-color: #fff;
}

.project .block .image {
  width: 336px;
  height: 444px;
}

.block-title {
  font-size: 20px;
  width: 312px;
  color: #333333;
  padding: 24px 12px;
}
