.smalls__fields {
  display: flex;
  column-gap: 12px;
}
.fields__btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 10px;
  width: 126px;
  height: 62px;
  background: #cf0a2c;
  border: 0;
  color: white;
}
.fields__disc {
  font-size: 16px;
  margin-top: 50px;
  margin-bottom: 24px;
  width: 540px;
  height: 40px;
}
@media (max-width: 576px) {
  .response-stud {
    display: flex;
    flex-direction: column;
  }
  .smalls__fields {
    display: flex;
    flex-direction: column;
  }
  .fields__disc {
    width: 100%;
    margin-bottom: 80px;
  }
}
