.dropdown {
  &__title {
    font-size: 30px;
    line-height: 44px;
    color: #333333;
    cursor: pointer;
  }

  &__drop {
    display: flex;
    margin-bottom: 40px;
  }

  &__arrow {
    cursor: pointer;
    width: 10px;
    margin-left: 25px;
    transform: rotate(-90deg);
    transition: all 0.3s;
  }
  &__arrow--active {
    transform: rotate(90deg);
  }

  &__item {
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s;
    div{
      margin-top: 0 !important;
      margin-bottom: 20px;
    }
  }
  &__item--active {
    max-height: 350px;
    overflow: visible;
  }
}
