.menuSidenav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  box-sizing: border-box;
  width: 100%;
  max-height: 50px;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  background-color: #cf0a2c;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 5px 25px;
  overflow: hidden;
  transition: all 0.5s;

  &__logoLink {
    width: 40px;
    height: 40px;
  }

  &__menuIcon {
    position: absolute;
    right: 25px;
    top: 15px;
  }

  &__navLinks {
    margin-top: 30px;
  }

  &__link {
    cursor: pointer;
    padding-right: 20px;
    margin-bottom: 36px;
    margin-left: 8px;
    text-decoration: none;
    white-space: nowrap;
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
    color: #ffffff;
    display: block;
    transition: all 0.3s;
  }

  &__arrow {
    display: inline;
    width: 15px;
    transition: all 0.4s;
  }

  &__disable {
    display: flex;
    margin-bottom: 36px;
    cursor: pointer;
  }
  &__disableLink {
    margin-bottom: 0;
  }

  &__submenu {
    margin-left: 15px;
  }

  &--active {
    max-height: 100vh;
  }
  @include sm-up {
    display: none;
  }
}
