.card {
  width: 250px;
  height: 320px;
  font-size: 16px;
  line-height: 122%;
  color: #333333;
  padding: 15px 20px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
  margin-top: 40px;
  margin-right: 20px;
  box-sizing: border-box;


  &__partner {
    width: 200px;
    margin-top: 30px;
  }
  &__license {
    width: 160px;
  }
  &__title, &__subtitle {
    margin-top: 50px;
    text-align: center;
  }

  &__subtitle {
    font-size: 15px;
    line-height: 22px;
    margin-top: 10px;
    color: #787878;
  }

  &__button{
    width: 100%;
    margin-top: 10px;
  }
}
