.header {

  color: #ffffff;

  &__inner {
    max-width: 550px;
    display: flex;
    flex-direction: column;
    padding: 40px 60px;
  }

  &__innerWhite {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    padding: 110px 60px;
  }

  &__whiteBackground{
    color: rgba(51, 51, 51, 1);
    padding: 24px;
    background-color: #ffffff;
  }
  &__titles {
    margin-bottom: 20px;
  }
}
