.slidSection {
  margin-top: 40px;

  display: flex;
  column-gap: 24px;

  &__slides-imgs {
    display: flex;
    flex-direction: column;

    img {
      width: 246px;
      height: 148px;

      margin-bottom: 34px;
    }
  }
}

.category_slides_text {
  p {
    font-size: 20px;

    span {
      font-size: 16px;
      color: #cf0a2c;
    }
  }
}
.completedProjects {
  margin-top: 40px;
  p {
    width: 414px;
    font-size: 20px;
    line-height: 122%;
  }
}
.block_elem {
  margin-top: 40px;
}
.element {
  display: flex;
  margin-bottom: 30px;
}
.tochka_div {
  position: relative;
}
.tochka {
  width: 18px;
  height: 18px;
  background-color: black;
  border-radius: 50%;
}
.tochka::after {
  position: absolute;
  content: '';
  width: 1px;
  height: 160%;
  background-color: black;
  left: 50%;
}
.text {
  width: 417px;
  margin-left: 25px;
  font-size: 20px;
}
.stagesOfWorkOrganization {
  margin: 120px 60px 143px 60px;

  &__lists-block {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    font-size: 35px;
    width: 606px;
  }

  &__list {
    margin-top: 43px;
    li {
      display: flex;
      align-items: center;
      column-gap: 26px;
      margin-bottom: 30px;
      img {
        width: 23px;
        height: 46px;
      }
      p {
        font-size: 16px;
        width: 367px;
      }
    }
  }
}
.finishedProjects {
  margin: 60px;

  &__block {
    display: flex;
    align-items: center;
    column-gap: 32px;
    margin-bottom: 40px;
    &__title {
      font-size: 35px;
    }

    &__desc {
      color: #cf0a2c;
      font-size: 16px;
    }
  }

  &__list {
    display: flex;
    column-gap: 24px;
    li {
      img {
        width: 336px;
        height: 444px;
      }
      p {
        font-size: 20px;
        width: 312px;
        padding: 12px 12px;
        font-weight: 700;
      }
    }
  }
}
.feedBackBlock {
  margin: 60px;
  width: 980px;
  height: 243px;
  background: #cf0a2c;
  padding: 40px;

  .feedBackBlock__p-desc {
    width: 569px;
    font-size: 20px;
    color: white;
  }

  &__group {
    display: flex;
    column-gap: 80px;
    position: relative;

    &__icons {
      &__vector {
        top: -85px;
        left: 60%;
        position: absolute;
      }
      &__email {
        transform: rotate(-15deg);
        position: absolute;
        left: 70%;
      }
    }
  }

  &__phone {
    display: flex;
    flex-direction: column;
    margin-top: 26px;
    div {
      display: flex;
      align-items: center;
      column-gap: 12px;
      margin-bottom: 16px;
    }
    span {
      font-size: 16px;
      color: white;
    }
  }

  &__email {
    margin-top: 26px;
    display: flex;
    column-gap: 12px;
    span {
      font-size: 16px;
      color: white;
    }
  }
}
