.sidenav {
  box-sizing: border-box;
  height: 100%;
  width: 300px;
  // min-width: 250px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #cf0a2c;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 25px;

  &__submenu {
    margin-left: 15px;
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s;
  }
  // &__submenu--active {
  //   max-height: 300px;
  //   overflow: visible;
  // }
  &__dropdown:hover {
    .sidenav__submenu {
      max-height: 300px;
      overflow: visible;
    }
    &:hover {
      img {
        transform: rotate(90deg);
      }
    }
  }

  &__arrow {
    display: inline;
    width: 15px;
    transition: all 0.4s;
  }

  &__link {
    cursor: pointer;
    padding-right: 20px;
    margin-bottom: 36px;
    margin-left: 8px;
    text-decoration: none;
    white-space: nowrap;
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
    color: #ffffff;
    display: block;
    transition: all 0.3s;
  }

  &__link:hover {
    color: #9b1d0d;
  }

  &__disable {
    display: flex;
    margin-bottom: 36px;
    cursor: pointer;
  }

  &__disableLink {
    margin-bottom: 0;
  }

  &__sublink {
    font-size: 14px;
  }

  &__logo {
    margin-left: 40px;
  }

  &__navLinks {
    padding-top: 10px;
    padding-bottom: 100px;
    padding-left: 40px;
  }
  &__rectangle {
    position: relative;
    top: 17px;
    right: 35px;
  }

  @include xs-only {
    display: none;
  }
}
.sidenav__second_list {
  margin-top: 110px;
  & li {
    display: flex;
    column-gap: 12px;
    color: white;
    margin-bottom: 20px;
    transition: 200ms;
    cursor: pointer;
  }
  & li:hover,
  svg:hover {
    color: #9b1d0d;
    stroke: #9b1d0d;
  }
}
