.contacts {
  margin-top: 48px;
  display: flex;
  column-gap: 230px;

  @media (max-width: 576px) {
    flex-direction: column;
    .map {
      margin-top: 40px;
    }

    &__title {
      font-size: 22px;
    }
  }
}
@media (max-width: 576px) {
  .map {
    margin-top: 40px;
  }
}
@media (max-width: 576px) {
  .container {
    margin: 15px;
  }
}
.contacts__ul {
  & li {
    column-gap: 12px;
    display: flex;
    align-items: center;
    margin-bottom: 28px;

    & span {
      font-size: 16px;
    }
  }
}
.contacts__description {
  display: flex;
  column-gap: 10px;
  & p {
    width: 328px;
    height: 180px;
    font-size: 16px;
    color: #333333;
  }

  @media (max-width: 576px) {
    & p {
      width: 268px;
      font-size: 14px;
    }
  }
}
