.success {
  display: flex;

  flex-direction: column;

  &__title {
    display: flex;
    column-gap: 12px;

    align-items: center;
    justify-content: center;

    span {
      font-weight: 700;
      font-size: 20px;
    }
  }
  &__text {
    font-size: 16px;
    width: 872px;
    margin-top: 24px;
  }

  &__feed {
    margin-top: 120px;
    p {
      font-size: 16px;
      margin-bottom: 12px;

      span {
        color: #cf0a2c;
        font-size: 16px;
      }
    }
    button {
      margin-top: 60px;
    }
  }

  @media (max-width: 576px) {
    &__title {
      span {
        font-size: 16px;
      }
    }

    &__text {
      font-size: 14px;
      width: 90%;
    }
  }

  &__feed {
    p {
      font-size: 14px;
      margin-bottom: 12px;

      span {
        color: #cf0a2c;
        font-size: 14px;
      }
    }
  }
}
