.field_label {
  font-size: 16px;
  margin-bottom: 10px;
  color: #333333;
}
.textField {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: 1px solid #333333;
  padding-left: 12px;
}
.small {
  width: 336px;
  height: 54px;
}
.fullWidth {
  width: 696px;
  height: 54px;
}

.textField::placeholder {
  font-size: 18px;
  color: #999999;
}
@media (max-width: 576px) {
  .textField {
    width: 95%;
  }
}
