.header__btn {
  margin-top: 24px;
  a {
    text-decoration: none;
  }
}
.list {
  margin-top: 40px;
  &__title {
    width: 300px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
  }
}
.list__titles {
  display: flex;
  flex-direction: column;
  &__li {
    display: flex;

    img {
      margin-bottom: 30px;
      cursor: pointer;
      margin-left: 16px;
      width: 40px;
      height: -webkit-fill-available;
    }
  }
}
.list_columns {
  display: flex;
  //   justify-content: space-between;
  column-gap: 148px;
}
.corporateCulture {
  margin-top: 120px;

  &__title {
    font-size: 35px;
  }
  &__info {
    display: flex;
    margin-top: 40px;

    div {
      width: 528px;
      height: 436px;
      background: #cf0a2c;
      color: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h3 {
        font-size: 30px;
        width: 370px;
        margin-bottom: 32px;
      }

      p {
        font-size: 16px;
        width: 370px;
        height: 40px;
      }
    }
  }

  @media (max-width: 576px) {
    &__info {
      flex-direction: column;

      div {
        width: 100%;

        h3 {
          font-size: 30px;
          width: 370px;
          margin-bottom: 32px;
          text-align: center;
        }

        p {
          font-size: 16px;
          width: 300px;
          height: 40px;
        }
      }
    }
  }
}
.ourValuesBlock {
  margin-top: 120px;

  &__ul {
    display: flex;
    margin-top: 45px;

    li {
      display: flex;
      flex-direction: column;
      align-items: center;

      span {
        width: 156px;
        height: 40px;
        font-size: 16px;
        text-align: center;
        color: #cf0a2c;
        margin-top: 15px;
      }

      img {
        width: 30px;
      }
    }
  }
}
.response {
  display: flex;
  margin-top: 120px;

  h2 {
    font-weight: 700;
    font-size: 35px;
    width: 516px;
  }
  p {
    width: 450px;
    height: 60px;
    font-size: 16px;
    margin-top: 24px;
  }
  button {
    margin-top: 27px;
  }

  &__images {
    display: flex;
    position: relative;
    margin-left: 120px;
  }
  &__image4 {
    width: 336px;
    height: 336px;
  }
  &__image5 {
    width: 217px;
    height: 217px;
    margin-top: 50px;
    z-index: 1000;
    position: absolute;
    right: 200px;
    top: 3px;
  }

  @media (max-width: 576px) {
    &__images {
      img {
        display: none;
      }
    }
    &__text {
      h2 {
        font-size: 25px;
        width: 346px;
      }
      p {
        width: 350px;
        font-size: 14px;
      }
    }
  }
}
.jobs {
  margin-top: 120px;

  &__block {
    display: flex;
    column-gap: 170px;

    &__second {
      p {
        width: 426px;
        height: 20px;
        font-size: 16px;

        span {
          color: #cf0a2c;
          font-size: 16px;
        }
      }
      img {
        margin-bottom: 120px;
      }
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__item_title {
    margin-bottom: 16px;
    font-size: 30px;
  }

  &__discription {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 433px;
    height: 68px;
    background: #ffffff;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
    margin-bottom: 16px;

    p {
      font-size: 20px;
    }
  }

  @media (max-width: 576px) {
    &__block {
      flex-direction: column;
    }
    .jobs__block__second {
      margin-top: 30px;
      img {
        display: none;
      }
    }
    &__discription {
      width: 100%;

      p {
        font-size: 16px;
      }
    }
  }
}
