main {
  margin-left: 300px; /* Same as the width of the sidenav */
  font-size: 28px; /* Increased text to enable scrolling */
  @include sm-down {
    margin-left: 250px;
  }
  @include xs-only {
    margin-left: 0;
  }
}
header {
  margin-left: 300px;
  min-height: 250px;
  @include sm-down {
    height: 100%;
    margin-left: 250px;
  }
  @include xs-only {
    height: 100%;
    margin-top: 50px;
    margin-left: 0;
  }
}
footer {
  margin-top: 50px;
  margin-left: 300px;
  @include sm-down {
    margin-left: 250px;
  }

  @include xs-only {
    margin-left: 0;
  }
}
