.block {
  display: flex;
  flex-direction: column;
  padding: 60px;
}
.isEvenIndex {
  display: flex;
  margin-bottom: 120px;
  column-gap: 114px;

  @media (max-width: 576px) {
    flex-direction: column;
    column-gap: 0;
    margin-bottom: 60px;

    img {
      width: 94vw !important;
      height: 308px !important;
    }

    .isEvenIndex__block__title {
      font-size: 24px;
      width: 400px;
    }
    .notEvenIndex__title {
      margin-top: 30px;
    }
  }

  &__block {
    height: 358px;
    position: relative;
    width: 515px;

    &__title {
      font-size: 35px;
      width: 426px;
    }
    a {
      text-decoration: none;
    }
    button {
      position: absolute;
      bottom: 0;
    }

    @media (max-width: 576px) {
      width: 100vw;
      button {
        bottom: 30px;
      }
    }
  }

  img {
    width: 515px;
    height: 358px;
  }
}
.content {
  display: flex;
  flex-wrap: wrap;
}
.test {
}
.text {
  margin-right: 10px; /* Расстояние между текстом и изображением */

  & p {
    width: 426px;
    height: 282px;
    line-height: 124%;
  }
}

.button {
  margin-top: 10px; /* Расстояние между текстом и кнопкой */
  width: 176px;
  height: 64px;
  padding: 0;

  & button {
    background: none;
    border: 0;
    color: white;
  }
  & button:hover {
    color: black;
  }
}
