.about {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  margin-bottom: 65px;

  &__img {
    background-image: url('../../assets/pic/About.png');
    // max-width: 90%;
    width: 420px; // assume this is the default size
    height: 400px;
    background-size: contain;
    background-repeat: no-repeat;
    @include sm-down {
      & {
        display: none;
      }
    }
  }
}

.aboutSection {
  display: flex;
  justify-content: space-between;

  &__img {
    @include sm-down {
      & {
        display: none;
      }
    }
  }

  &__paragraph {
    width: 512px;
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 122%;
    color: #cf0a2c;
    margin-bottom: 16px;
  }

  &__text {
    font-size: 16px;
    line-height: 122%;
    color: #333333;
  }
}
@media (max-width: 576px) {
  .protection-text {
    font-size: 14px;
    width: 350px;
  }
}
