.vacancies__disc {
  margin-top: 62px;
  display: flex;
  column-gap: 16px;

  img {
    width: 32px;
    height: 32px;
  }
  p {
    font-size: 16px;
    width: 516px;

    a {
      color: #cf0a2c;
    }
  }
}
.vacancies {
  &__title {
    font-size: 35px;
    margin-bottom: 16px;
  }
  &__items {
    margin-top: 40px;

    a {
      color: #333333;
      text-decoration: none;
    }
  }
  &__block {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 433px;
    height: 68px;
    background: #ffffff;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
    font-size: 20px;
    margin-bottom: 24px;
  }

  @media (max-width: 576px) {
    &__block {
      width: 100%;
      font-size: 16px;
    }
  }
}
